import React, { useState } from 'react';
import styled from 'styled-components';
import { navArr } from '../../shared/route';
import { Link } from 'react-router-dom';


interface INavInsideProps {
  visible: boolean;
  menu: string;
  onClick?(): void;
}

const NavInside = (props: INavInsideProps) => {
  const [width, setWidth] = useState<number>(window.innerWidth);

  window.addEventListener('resize', () => setWidth(window.innerWidth))

  if (width >= 1280) {
    if (navArr.find(i => i.key === props.menu)) {
      const menu = navArr.find(i => i.key === props.menu);
      return (
        <NavInsideStyle {...props}>
          <div style={{ width: 232 }}>
            <div style={{ fontFamily: "JSDongKang-Bold", fontSize: '20pt', marginBottom: 10 }}>{menu?.title}</div>
            <div>{menu?.desc.map(i => (
              <div key={i}>{i}</div>
            ))}</div>
          </div>
          {menu?.children.map(i => (
            <NavInsideInside key={i.title}>
              <div style={{ fontFamily: "JSDongKang-Bold", marginBottom: 10 }}>{i.title}</div>
              <div>{i.links.map(j => (
                <div key={j.name} style={{ marginBottom: 10 }}>
                  <Link to={"/" + props.menu + "/" + j.href} onClick={props.onClick}>{j.label}</Link>
                </div>
              ))}</div>
            </NavInsideInside>
          ))}
        </NavInsideStyle>
      );
    }
    return (
      <NavInsideStyle visible={false} menu="" />
    );
  } else {
    return (
      <NavInsideMobileStyle {...props}>
        <div>
          대분류 글자 24pt
        </div>
        <div>
          프로젝트
          <div>
            디스코드 챗봇
            <div>
              코로나19 알림봇
            </div>
            <div>
              AlchemyBOT
            </div>
          </div>
          <div>
            중분류 글자 20pt
            <div>
              소분류 글자 18pt
            </div>
          </div>
        </div>
        <div>
          프로젝트
          <div>
            디스코드 챗봇
            <div>
              코로나19 알림봇
            </div>
            <div>
              AlchemyBOT
            </div>
          </div>
          <div>
            중분류 글자 20pt
            <div>
              소분류 글자 18pt
            </div>
          </div>
        </div>
      </NavInsideMobileStyle>
    )
  }
}

const NavInsideStyle = styled.div<INavInsideProps>`
  transition: .3s ease-in-out;
  background: white;
  height: 200px;
  /* padding-left: 5px; */
  padding: 50px;
  border-bottom: 5px solid rgb(255, 204, 102);
  position: relative;
  z-index: -5;
  display: flex;

  /* display: ${props => props.visible ? "block" : "none"}; */
  top: ${props => props.visible ? "-10px" : "-300px"};
  opacity: ${props => props.visible ? "1" : "0"};

  @media only screen and (min-width: 1620px) {
    padding: 50px calc((100% - 1520px) / 2);
  }
  
  & a {
    text-decoration: none;
    color: black;
  }
`;

const NavInsideInside = styled.div`
  width: 160px;
  margin-right: 40px;
  border-right: 1px solid #6d788f;
`;

const NavInsideMobileStyle = styled.div<INavInsideProps>`
  /* transition: opacity .3s ease-in-out, display 0s none; */
  transition-property: opacity, display;
  transition-duration: .3s, 0s;
  transition-timing-function: ease-in-out;
  transition-delay: 0s, .3s;

  height: calc(100vh);
  background: rgba(255, 255, 255, 0.9);
  margin-top: -5px;
  padding: 50px;

  width: 100%;
  box-sizing: border-box;
  /* display: ${props => props.visible ? "block" : "none"}; */
  opacity: ${props => props.visible ? 1 : 0};

  > div {
    font-family: "JSDongkang-Bold";
    font-size: 24pt;
    margin-bottom: 50px;
    > div {
      font-family: "JSDongkang-Bold";
      font-size: 20pt;
      padding-left: 50px;
      margin-top: 30px;
      > div {
        font-size: 18pt;
        margin-top: 20px;
      }
    }
  }
`;


export default NavInside;